import { Righteous } from "next/font/google";
import Link from "next/link";

const font = Righteous({
  subsets: ["latin"],
  weight: "400",
});

export default function Logo() {
  return (
    <div className="logo">
      <Link href={"./"}>
        <span>Logo</span>
      </Link>
    </div>
  );
}
